.header-container {
    display: flex;
    width: 100%;
    height: 5rem;
    z-index: 10;
    background-color: #180D43;
    position: fixed;
}

.left-side {
    display: flex;
    flex-basis: 40%;
    align-items: center;
    padding-left: 1rem;
}

#title {
    color: #cfd8eb;
    padding-left: 1vw;
    font-family: 'Nunito';
}

.right-side {
    display: flex;
    flex-direction: row;
    flex-basis: 70%;
    align-items: center;
    justify-content: flex-end;
    font-family: 'Oswald';
}

#link {
    cursor: pointer;
    color: #fff;
    z-index: 100;
    font-size: 1rem;
    padding: 0rem;
    margin: 0.7rem;
}

#link:hover {
    font-size: 1rem;
    color: rgb(226, 223, 223);
    animation: fadeIn 2s;
}

#menu {
    cursor: pointer;
    color: #cfd8eb;
    z-index: 100;
    font-size: 1.3rem;
    padding-right: 1rem
}

.menu-dropdown-right {
    width: 20%;
    height: 210vw;
    display: block;
    position: absolute;
    z-index: 99;
    background-color: #2a1e57;
    right: 0;
    animation: fadeIn 1s;
}

.menu-dropdown-left {
    width: 80%;
    height: 210vw;
    display: block;
    position: absolute;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.5);;
    left: 0;
    animation: fadeIn 1s;
}

.menu-dropdown-hide {
    animation: fadeOut 1s;
    display: none;
}

.links {
    display: flex;
    flex-direction: column;
    padding-top: 4rem;
    padding-left: 2rem;
}

.widescreen-links {
    display: flex;
    flex-direction: row;
}

@media only screen and (max-width: 1030px) {
    .menu-dropdown-right {
        width: 40%;
    }

    .menu-dropdown-left {
        width: 60%;
    }
}

@media only screen and (max-width: 600px) {
    .menu-dropdown-right {
        width: 60%;
    }

    .menu-dropdown-left {
        width: 40%;
    }
}

@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes fadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
}